body {
  background-color: rgb(255, 255, 255);
  color: #2b2f33;
  font-family: 'Montserrat', sans-serif;
  font-weight: 300;
  margin-bottom: 100px;
}

.header {
  font-size: 7.5rem;
  font-family: 'Playfair Display', serif;
  line-height: 1;
  color: #3f4d5a;
}

.main-row {
  margin-top: 80px;
}

nav {
  box-shadow: 0 2px 2px 2px rgba(0, 0, 0, 0.2);
}

hr {
  border-color: rgba(255, 255, 255, 0.5);
}

.link:hover {
  cursor: pointer;
  color: rgb(228, 24, 51);
  text-decoration: none;
}

.overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  opacity: 0;
  transition: .5s ease;
}

.img-div {
  position: relative;
  padding-right: 0;
  padding-left: 0;
  margin-right: 0;
  text-align: right;
}

.img-div:hover .overlay {
  opacity: 1;
}

.footer {
  background-color:#343a40;
  position: fixed;
  box-shadow: 2px 0 4px 2px rgba(0, 0, 0, 0.5);
  bottom: 0;
  margin-top: 50px;
}

.fab-div {
  text-align: right;
}

.fab {
  color: #7d868d;
  font-size: 3rem;
  padding: 15px;
}

.fab:hover {
  color: white;
  cursor: pointer;
}



.animation-row {
  text-align: center;
}

.deck-img {
  width: 100%;
  max-width: 200px;
  border: 1px rgba(0, 30, 93, 0.15) solid;
  margin: 0;
  margin-right: 0;
  margin-left: 0;
  padding: 0;
  opacity: 0.5;
}

.deck-img:hover {
  opacity: 1;
  border: 1px rgb(65, 183, 212) solid;
  cursor: pointer;
}

.gif-col {
  padding-left: 5px;
  padding-right: 5px;
  padding: 5px;
  margin-top: 5px;
}

.stage {
  max-width: 450px;
}

.deck-row {
  margin-top: 220px;
}
@media all and (max-width: 767px) {
  .deck-row {
    margin-top: 20px;
  }
}
@media all and (max-width: 480px) { 
  .name {
    font-size: 18vw;
  }
  .main-row {
    margin-top: 10vw;
  }

  .deck-img {
    width: 100px;
  }
}